<template>

<section class="m-popup f8-bar f8-bar--visual-fullscreen" :class="{ 'f8-bar--effect-hide-to-fade': isPopupClose }">
 <div class="m-popup__perspective f8-bar__perspective" @click.self="this.$emit('popupClose')">

   <!-- Hotmat Contact Popup
   ============================================= -->
   <section v-if="popupType == 'contact'" class="m-popup__card f8-card u8-curved u8-box-shadow">
     <header class="m-popup__card-head f8-card__head">
       <i class="bi bi-at u8-text-size-lg u8-line-height-0"></i>
       <h3 class="m-popup__card-heading">Kontakta oss</h3>
     </header>
     <div class="m-popup__card-body f8-card__body u8-chrome-pretty-scroll">
       <p>Har du ngra frgor eller funderingar s r du vlkommen att mejla oss. Vi svarar oftast inom 24 timmar.</p>
       <address>
         Fretag: <a class="f8-link f8-link--effect-underline" href="https://www.hotmat.se/">Hotmat.se</a><br>
         E-post : <a class="f8-link f8-link--effect-underline" href="mailto:info@hotmat.se">info@hotmat.se</a>
       </address>
     </div>
   </section>




   <!-- Hotmat Conditions Popup
   ============================================= -->
   <section v-else-if="popupType == 'conditions'" class="m-popup__card f8-card u8-curved u8-box-shadow">
     <header class="m-popup__card-head f8-card__head">
       <i class="bi bi-file-text u8-text-size-lg u8-line-height-0"></i>
       <h3 class="m-popup__card-heading">Kpvillkor</h3>
     </header>
     <div class="m-popup__card-body f8-card__body u8-chrome-pretty-scroll">
       <h4 class="u8-text-color-primary u8-font-weight-bold u8-my">Bestllningsvillkor</h4>
       <p>
         Det r restaurangen som bestmmer hur snabbt dom kan laga maten och leveranstiden rknas automatiskt med GPS. Leveranstiden varierar ofta med tiden p dygnet och vi reserverar oss fr eventuella drjsml till fljd av lng orderk eller trafikproblem.
         <br>
         Fr srskilt lnga leveranstider har restauranger/budfirman ansvaret att meddela kunden s snart som mjligt.
         <br>
         Personliga uppgifter som fylls i bestllningsformulret ska vara korrekt och tillhra den som bestller maten.  Legitimation mste visas vid leveransen.
         <br>
         Hotmat.se ansvarar inte fr utebliven leverans till fljd av otillrcklig eller felaktig information i bestllningsformulret.
         <br>
         Man mste ha fyllt 18 r fr att bestlla frn Hotmat.se
       </p>

       <h4 class="u8-text-color-primary u8-font-weight-bold">Personuppgifter</h4>
       <p>
         Vid bestllning via hotmat.se lagras all ndvndig information (personuppgifter) efter personuppgiftslagen (PuL). Informationen behvs fr att ge en trygg och bra tjnst. Nr en bestllning grs via hotmat.se godknner du samtidigt att all information fr lagras, sparas och bearbetas i hotmat.se dataregister enligt PuL. All dataregister fr enligt PuL presenteras fr parter (ssom restaurangen och utkrningsfirman) vid taganden som berr dig som kund/bestllare. Uppgifterna kan ven komma att anvndas av hotmat.se i marknadsfringssyfte och fr att sammanstlla statistik, dvs i syfte att frbttra vr tjnst. Hos hotmat.se lagras inte personuppgifter lngre tid n ndvndigt.
       </p>

       <h4 class="u8-text-color-primary u8-font-weight-bold">Priser</h4>
       <p>
         Alla priser p Hotmat.se r inklusive moms. Det r restaurangens ansvar att hlla hemsidan uppdaterat med eventuella prisndringar.
         <br>
         Utkrningspriser beror p avstndet mellan restaurangen och leveransadressen och rknas automatiskt med GPS.
       </p>

       <h4 class="u8-text-color-primary u8-font-weight-bold">Leverans</h4>
       <p>
         Utkrningen skts av varje enskild restaurang eller budfirma. Restaurangen/Budfirman har ansvar fr utkrningen.
         <br>
         Det r kundens ansvar att vara p plats nr maten levereras.
         <br>
         Om maten inte r levererad inom 1 timme efter restaurangens leveranstid ber vi dig att kontakta oss via mail och bifoga bestllningens ID s fr du pengarna tillbaka.
       </p>

       <h4 class="u8-text-color-primary u8-font-weight-bold">ngerrtt</h4>
       <p>
         Vid bestllning av livsmedel gller ingen ngerrtt. Utdrag ur Konsumentverkets informationsbroschyr om Distans- och hemfrsljningslagen:
         <br>
         Vid distansavtal och hemfrsljningsavtal har du inte ngon ngerrtt om du kper livsmedel eller andra varor fr ett hushlls lpande frbrukning
       </p>

       <h4 class="u8-text-color-primary u8-font-weight-bold">Missbruk</h4>
       <p>
         Kundens IP-adress och andra vrdefulla information om Internetleverantr lagras i databasen fr att vid eventuellt missbruk av tjnsten kunna spra upp bestllaren. Vi polisanmler och utreder alla typer av missbruk av vra tjnster p Hotmat.se.
         <br>
         Vi fljer Allmnna reklamationsnmndens rekommendationer.
       </p>

       <h4 class="u8-text-color-primary u8-font-weight-bold">Betalning och skerhet</h4>
       <p>
         - Kortbetalning : Betalningen sker via DIBS. Vi lagrar inga uppgifter om din bank eller ditt kort. Vi sparar transaktionens ID som skickas frn DIBS till oss fr att kunna spra betalningen vid eventuella problem.
       </p>
       <p>
         - Fakturabetalning : Nr du betalar via faktura handlar du tryggt och enkelt. Du behver aldrig uppge dina kortuppgifter, och betalar alltid efter att du ftt hem dina varor. Du behver aldrig lmna ut kortuppgifter
         <br>
         &nbsp;&nbsp; En aviavgift om 0 kr per kp tillkommer. Om betalningen uteblir tillkommer en pminnelseavgift om 60,00 kr samt drjsmlsrnta om 20.00 %. Vid kptillfllet grs en kreditprvning som i vissa fall innebr att en kreditupplysning tas. D kommer du f en kopia p kreditupplysningen per post. Personuppgifter hanteras i enlighet med gllande lagstiftning.
       </p>
       <p>
         - Kontant : Med kontantbetalning bestller du maten och betalar nr du fr maten.
       </p>

       <h4 class="u8-text-color-primary u8-font-weight-bold">Vad r SSL?</h4>
       <p>
         Secure Sockets Layer (SSL) r en skerhetsmekanism som uppfanns till Netscape och anvnds fr att kryptera kommunikationen mellan tv enheter. Ofta r ena sidan en webbserver (ex. onlinebutik) och den andra r en webblsare (ex. Internet Explorer, Chrome eller Firefox). Tanken med att kryptera data som snds till och frn dessa tv enheter r att ingen annan p ntverket (oftast internet) ska kunna avlyssna eller manipulera informationen. Handlar man p internet s faller det sig naturligt att man anvnder SSL fr att kryptera ex. kreditkortsinformation.
       </p>

       <h4 class="u8-text-color-primary u8-font-weight-bold">ndringar</h4>
       <p>
         Hotmat.se har rtt att ndra sina kpvillkor utan att meddela kunden. Kunden godknner kpvillkoren genom att bestlla p hemsidan.
       </p>

       <address class="u8-mb">
         Hotmat.se Sverige AB
			   Huvudkontor: Sgstuvgen 3D, 14149 Huddinge
         <br>
         Org nr : 559001-2893
         <br>
         Momsreg.nr : SE559001289301
         <br>
         E-post : info@hotmat.se
       </address>

       <p>
         Fr mer information kan du kontakta oss via E-post eller kontakta varje enskild restaurang via telefon.
       </p>
     </div>
   </section>




